//
@mixin reset-style-list {
  margin: 0;
  padding: 0;

  list-style: none;
}
//
@mixin button {
  text-align: center;
  text-decoration: none;

  color: $white;

  cursor: pointer;
}

@mixin button-red {
  padding: 11px 9px 9px 9px;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border-radius: 0;

  background-color: $red-button;
  border-color: $red-button;

  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: $red;
  }

  &:focus-visible {
    outline: 2px solid $green;
    outline-offset: 2px;
  }

  &:active {
    opacity: 0.5;
  }
}

//
@mixin font-pt-sans {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: $black;
}

@mixin font-pt-sans-legend {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
  text-transform: uppercase;
  color: $black;
}

@mixin font-pt-sans-label {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  text-transform: uppercase;
  color: $black;
}
