//    
.popup-lost {
  display: none;
}
//------------C   js
.popup-lost--page-form-lost {
  display: block;
}

.popup-lost__form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 80%;
  box-sizing: border-box;

  border-top: 7px solid $red;

  @media (min-width: $tablet-width) {
    width: 620px;
  }
}
//---------------     js
.popup-lost--page-form-lost .popup-lost__form {
  position: static;
  transform: translate(0, 0);

  margin: 0 auto;

  border: 1px solid $grey-border;
  border-top: 7px solid $red;
}

.popup-lost__wrapper-user {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding: 30px 20px 20px;
  background-color: $white;

  @media (min-width: $tablet-width) {
    padding: 46px 80px 37px;
  }
}

.popup-lost__wrapper-user p {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 13px;

  @media (min-width: $tablet-width) {
    width: 47.8%;
  }
}

.popup-lost__wrapper-user p:last-child {
  width: 100%;
  margin-bottom: 0;
}

.popup-lost__wrapper-user label {
  margin-bottom: 11px;

  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: $black;
}

.popup-lost__wrapper-user input,
.popup-lost__wrapper-user textarea {
  @include font-pt-sans;

  color: $grey-text;

  border: 2px solid $grey-dark-svg;
  border-radius: 0;

  &::placeholder {
    color: rgba($grey-text, 0.7);
  }
}

.popup-lost__wrapper-user textarea {
  min-height: 114px;
  resize: none;
}

.popup-lost__wrapper-button {
  padding: 20px;

  background-color: $grey-border;

  @media (min-width: $tablet-width) {
    padding: 37px 80px;
  }
}

.popup-lost__button-submit {
  @include button;
  @include button-red;

  display: block;
  width: 100%;
}

.popup-lost__field-error {
  outline: 2px solid $red;
}

//   
.popup-map {
  display: none;
}

.popup-map__wrapper-iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 78.5%;
  padding-top: 80%;

  @media (min-width: $tablet-width) {
    padding-top: 55%;
  }

  @media (min-width: $desktop-width) {
    width: 943px;
    padding-top: 449px;
  }
}

.popup-map__wrapper-iframe iframe {
  position: absolute;
  top: 0;
  left: 0;

  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

//   
.popup-search {
  display: none;

  @media (min-width: $tablet-width) {
    position: relative;
    display: none;
  }
}

//------------   js
.popup-search--nojs {
  @media (min-width: $tablet-width) {
    position: absolute;
    left: 240px;
    top: 20px;
    display: block;
    width: 32%;
  }

  @media (min-width: $desktop-width) {
    left: 300px;
  }
}

.popup-search__wrapper-input {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 60%;
}

//     
.popup-cart {
  display: none;
}

.popup-cart__wrapper-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;

  width: 80%;
  box-sizing: border-box;

  border-top: 7px solid $red;
  background-color: $white;

  @media (min-width: $tablet-width) {
    width: 620px;
  }
}

.popup-cart__wrapper-text {
  padding: 120px 15px 40px;

  @media (min-width: $tablet-width) {
    padding: 67px;
  }
}

.popup-cart__wrapper-text p {
  position: relative;

  width: 90%;
  margin: 0 auto;

  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: $black;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 420px;

    font-size: 24px;
    line-height: 30px;
  }
}

.popup-cart__wrapper-text p::before {
  content: "";
  position: absolute;
  top: -80px;
  left: 50%;
  transform: translate(-50%, 0);

  width: 66px;
  height: 66px;

  background-color: $green-dark;
  background-image: url("../img/back-icon-mark.svg");
  background-repeat: no-repeat;
  border-radius: 50%;
  background-position: center center;

  @media (min-width: $tablet-width) {
    top: 50%;
    left: -10px;
    transform: translate(0, -50%);
  }
}

.popup-cart__wrapper-buttons {
  padding: 37px;
  background-color: $grey-border;
  text-align: center;
}

.popup-cart__button-order {
  @include button;
  @include button-red;

  display: inline-block;
  vertical-align: middle;

  width: 100%;
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    width: 192px;
    margin-bottom: 0;
    margin-right: 4px;
  }
}

.popup-cart__button-continue {
  @include button;

  display: inline-block;
  vertical-align: middle;
  width: 100%;
  padding: 11px 9px 9px 9px;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border-radius: 0;

  color: $black;
  background-color: $white;

  @media (min-width: $tablet-width) {
    width: 192px;
  }
}

// 
.popup--opened {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;

  display: block;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;

  background-color: rgba($black, 0.9);
}

//  
.popup__button-close {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 1000;

  display: block;
  width: 21px;
  height: 21px;

  border: none;
  background-color: transparent;

  transition: all 0.2s ease-in-out;
}
//------------   js
.popup-lost--page-form-lost .popup__button-close {
  display: none;
}

.popup__button-close--search {
  display: block;
  width: 30px;
  height: 30px;
}

//------------   js
.popup-search--nojs .popup__button-close--search {
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.popup__button-close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);

  width: 29px;
  height: 2px;
  background-color: $red;
}

.popup__button-close--search::after {
  width: 42px;

  background-color: $white;
}

.popup__button-close::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%) rotate(-45deg);

  width: 29px;
  height: 2px;

  background-color: $red;
}

.popup__button-close--search::before {
  width: 42px;

  background-color: $white;
}

/*=========================*/
//hover
.popup-cart__button-continue:hover {
  background-color: $green;
  color: $white;
}

.popup__button-close:hover::after,
.popup__button-close:hover::before {
  background-color: $yellow;
}

//focus
.popup-cart__button-continue:focus-visible,
.popup__button-close:focus-visible,
.popup-lost__wrapper-user input:focus-visible,
.popup-lost__wrapper-user textarea:focus-visible {
  outline: 2px solid $green;
  outline-offset: 2px;
}

//active
.popup-cart__button-continue:active,
.popup__button-close:active {
  opacity: 0.5;
}
